@mixin boxSwicth() {
     .switch {
          position: relative;
          margin: 0 .35rem;
          &__input {
               position: absolute;
               cursor: pointer;
               top: 0;
               right: 0;
               left: 0;
               bottom: 0;
               width: 100%;
               height: 100%;
               margin: 0;
               opacity: 0;
               z-index: 1;
               &:checked + .switch__label {
                    background-color: $yellow;
                    &::before {
                         left: calc(100% - 22px);
                         border: 2px white solid;
                         background-color: $yellow;
                         content: ""; } } }
          &__label {
               display: block;
               position: relative;
               width: 50px;
               height: 23px;
               background-color: white;
               border: 1px solid $yellow;
               border-radius: 25px;
               transition: 0.4s;
               margin-bottom: 0.15rem;
               &::before {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: auto;
                    left: 1px;
                    bottom: 0;
                    width: 21px;
                    height: 21px;
                    border-radius: 100%;
                    border: 2px white solid;
                    background-color: $yellow;
                    color: white;
                    transition: 0.4s;
                    content: ""; } } } }
@mixin fontTitle($fontColor) {
     $fontSize1400: 38px;
     $fontSize1200: 36px;
     $fontSize992: 38px;
     $fontSize: 28px;
     font-family: $fontTitle;
     font-weight: 800;
     font-size: $fontSize;
     color: $fontColor;
     line-height: 1em;
     @media screen and (min-width: 992px) {
