@mixin styleButton($backgroundColor: false) {
     @if $backgroundColor {
          button {
               border: 3px solid rgba($backgroundColor, .5); } }
     @else {
          button {
               border: 3px solid rgba($lightBlue, 1); } }
     overflow: hidden;
     width: 280px;
     height: 63px;
     box-sizing: border-box;
     border-radius: 10px;
     @media screen and (min-width: 992px) {
          width: 353px; }
     @media screen and (min-width: 1200px) {
          width: 353px; }
     @media screen and (min-width: 1400px) {
          width: 353px; }
     button {
          width: 100%;
          height: 100%;
          box-shadow: none;
          text-decoration: none;
          border-radius: 10px;
          text-align: center;
          $fontSize1400: 24px;
          $fontSize1200: 24px;
          $fontSize992: 24px;
          $fontSize: 18px;
          font-family: $fontButtom;
          line-height: 1em;
          font-size: $fontSize;
          color: white;
          @if $backgroundColor {
               background: $backgroundColor; }
          @else {
               background: $lightBlue;
               transition: .5s ease-in all; }
          &:hover {
               background: $backgroundColor;
               border: 3px solid rgba($backgroundColor, 1);
               color: white; }
          @media screen and (min-width: 992px) {
               font-size: $fontSize992;
               line-height: 1em; }
          @media screen and (min-width: 1200px) {
               font-size: $fontSize1200;
               line-height: 1.5em; }
          @media screen and (min-width: 1400px) {
               font-size: $fontSize1400;
               line-height: 1.5em; } }
     &:focus,
     &.active {
          outline: none;
          box-shadow: none; } }
