a {
     text-decoration: none;
     &:focus,
     &:hover {
          outline: transparent;
          box-shadow: none; } }
h1,
h2,
h3,
p {
     margin: 0;
     &.txtjustify {
          text-align: justify; } }
.contentBlur {
     &.active {
          filter: blur(2px); } }
.spanAbsulote {
     position: absolute;
     top: 4.25rem;
     left: -.5rem;
     @include fontFree(#000000, 12px,12px,12px,12px, $fontDescriptionMedium);
     @media screen and (min-width: 992px) {
          top: 3.5rem;
          left: -1rem; } }
p {
     &.FontLight {
          font-family: $fontDescriptionMedium; }
     @include fontDescription(black);
     a {
          @include fontDescription(black); }
     &.legal {
          font-size: 12px !important; }
     span.bold,
     &.bold {
          font-family: $fontTitle !important;
          word-spacing: .25rem; }
     &.big {
          font-size: 25px;
          color: $grayLight2;
          line-height: 1em;
          @media screen and (min-width: 992px) {
               font-size: 30px;
               line-height: 1em; }
          @media screen and (min-width: 1200px) {
               font-size: 40px;
               line-height: 1.25em; }
          @media screen and (min-width: 1400px) {
               font-size: 46px;
               line-height: 1.25em; } }
     &.mediun {
          font-size: 12px;
          line-height: 1em;
          @media screen and (min-width: 992px) {
               font-size: 12px;
               line-height: 1em; }
          @media screen and (min-width: 1200px) {
               font-size: 14px;
               line-height: 1.5em; }
          @media screen and (min-width: 1400px) {
               font-size: 14px;
               line-height: 1.5em; } } }
.boxButton {
     @include styleButton($pink);
     &.fullLarge {
          width: 100% !important; }
     &.large {
          width: 100%;
          @media screen and (min-width: 992px) {
               width: 250px; }
          @media screen and (min-width: 1200px) {
               width: 280px; }
          @media screen and (min-width: 1400px) {
               width: 300px; } }
     &.bggris {
          @include styleButton(#fafaf4);
          button {
               color: rgba(#00ae9e,.95); } } }
.boxButtonLink {
     @include styleButtonLink();
     &.large {
          width: 100%;
          @media screen and (min-width: 992px) {
               width: 250px; }
          @media screen and (min-width: 1200px) {
               width: 280px; }
          @media screen and (min-width: 1400px) {
               width: 300px; } } }
.boxButtonTransparent {
     @include styleButtonTransparent(); }
.boxLink {
     @include styleLink(black, black); }
.boxTitle {
     h1,
     h2 {
          @include fontTitle($pink);
          letter-spacing: 0px; }
     h2,
     h1 {
          &.small {
               font-size: 18px;
               color: $gray;
               @media screen and (min-width: 992px) {
                    font-size: 24px; }
               @media screen and (min-width: 1200px) {
                    font-size: 26px; }
               @media screen and (min-width: 1400px) {
                    font-size: 28px; } }
          &.mediun {
               font-size: 28px;
               color: $gray;
               @media screen and (min-width: 992px) {
                    font-size: 33px; }
               @media screen and (min-width: 1200px) {
                    font-size: 38px; }
               @media screen and (min-width: 1400px) {
                    font-size: 42px; } } }
     h4 {
          @include fontFree($gray, 18px,16px,14px,12px, $fontSubTitle);
          line-height: 1.5em; } }
.txtUppercase {
     text-transform: uppercase !important; }
.boxSubTitle {
     h2,
     h3,
     h4,
     h5 {
          @include fontSubTitle($lightBlue, $fontSubTitle);
          &.small {
               font-size: 18px;
               color: $gray;
               @media screen and (min-width: 992px) {
                    font-size: 24px; }
               @media screen and (min-width: 1200px) {
                    font-size: 26px; }
               @media screen and (min-width: 1400px) {
                    font-size: 28px; } } } }
.relative {
     position: relative; }

.fullHeight {
     height: 100% !important; }
.fullwidth {
     width: 100% !important; }
.txtCenter {
     text-align: center !important; }
.txtUpperCase {
     text-transform: uppercase !important; }
.txtColorGray {
